<script>
export default {
  data() {
    return {
      filter: "",
      title: "",
      status: "",
      bulk: false,
      dialog: false,
      dialog1: false,
      dialog2: false,
      dialogUpload: false,
      loading: false,
      tab: "Applied",
      applied_count: 0,
      shortlisted_count: 0,
      invited_count: 0,
      interviewed_count: 0,
      hired_count: 0,
      archived_count: 0,
      rejected_count: 0,
      all_count: 0,
      application: {},
      applications: [],
      application_histories: [],
      job_title: {},
      career_profile: {},
      levels: ['Pre-Screening', 'Screening Interview', 'Phone or Skype Interview', 'First, In-Person Interview', 'Second, In-Person Interview', 'Third or Final In-Person Interview', 'Talent Assessment or Personality Type Quizzes', 'Background Check', 'Reference Checks', 'Job Offer'],
      pagination: { page: 1, size: 10 },
    };
  },
  created() {
    this.$store.dispatch("dictionary/getIndustries");
    this.$store.dispatch("company/getCompanyBranches");
    this.$store.dispatch("company/getJobTitles");
    this.$store.dispatch("company/getEmploymentTypes");
    var position = JSON.parse(localStorage.getItem("position"));
    if (position) {
      this.job_title = position;
      this.job_title.job_id = position.id;
    }
    this.getApplications();
  },
  methods: {
    filterElements(tab) {
      this.tab = tab.props.name;
      this.getApplications();
    },
    selectAll(val) {
      this.application.application_ids = val;
    },
    reviewCv(profile_id) {
      localStorage.setItem("profile_id", JSON.stringify(profile_id));
      this.$router.push("/career/profile");
    },
    handleCurrentChange(val) {
      this.pagination.page = val;
      this.getApplications();
    },
    handleSizeChange(val) {
      this.pagination.size = val;
      this.getApplications();
    },
    getApplications() {
      this.job_title.id = this.job_title.job_id;
      localStorage.setItem("position", JSON.stringify(this.job_title));
      let filter = this.job_title;
      filter.page = this.pagination.page,
        filter.size = this.pagination.size,
        filter.search = this.search,
        this.$endpoint.getApplications(filter).then(
          (res) => {
            this.loading = true;
            var tab = this.tab;
            this.pagination.meta = res.data.meta;
            var records = res.data.data;
            var applied = records.filter(function (row) {
              return row.status == "Applied";
            });
            var shortlisted = records.filter(function (row) {
              return row.status == "Shortlisted";
            });
            var invited = records.filter(function (row) {
              return row.status == "Invited";
            });
            var interviewed = records.filter(function (row) {
              return row.status == "Interviewed";
            });
            var hired = records.filter(function (row) {
              return row.status == "Hired";
            });
            var archived = records.filter(function (row) {
              return row.status == "Archived";
            });
            var rejected = records.filter(function (row) {
              return row.status == "Rejected";
            });
            this.applied_count = applied.length;
            this.shortlisted_count = shortlisted.length;
            this.invited_count = invited.length;
            this.interviewed_count = interviewed.length;
            this.hired_count = hired.length;
            this.archived_count = archived.length;
            this.rejected_count = rejected.length;
            this.all_count = records.length;

            if (this.tab == "All") {
              this.loading = false;
              this.applications = records;
            } else {
              var filtered = records.filter(function (row) {
                return row.status == tab;
              });
              this.loading = false;
              this.applications = filtered;
            }
          },
          (error) => {
            this.loading = false;
            this.message =
              (error.response &&
                error.response.data &&
                error.response.data.message) ||
              error.message ||
              error.toString();
            this.$notify({
              title: "Error",
              message: this.message,
              type: "error",
            });
          }
        );
    },
    edit(data) {
      this.application = data;
      this.dialog = true;
    },
    update() {
      this.loading = true;
      this.$endpoint.saveApplication(this.application).then(
        (res) => {
          this.loading = false;
          this.getApplications();
          this.application = {};
          this.dialog = false;
          this.$notify({
            title: "Success",
            message: res.data.message,
            type: "success",
          });
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
    batch() {
      if (!this.application.application_ids) {
        alert("Please select records to perform batch action");
        return
      } else {
        this.loading = true;
        this.$endpoint.batchApplication(this.application).then(
          (res) => {
            this.loading = false;
            this.getApplications();
            this.application = {};
            this.dialog = false;
            this.bulk = false;
            this.history = false;
            this.$notify({
              title: "Success",
              message: res.data.message,
              type: "success",
            });
          },
          (error) => {
            this.loading = false;
            this.message =
              (error.response &&
                error.response.data &&
                error.response.data.message) ||
              error.message ||
              error.toString();
            this.$notify({
              title: "Error",
              message: this.message,
              type: "error",
            });
          }
        );
      }
    },
    getCareerProfile(profile_id) {
      this.$endpoint.getCareerProfile(profile_id).then(
        (res) => {
          this.loading = false;
          this.career_profile = res.data.data;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
    downloadCv(data) {
      this.loading = true;
      this.$endpoint.downloadCv(data).then(
        () => {
          this.loading = false;
          this.$notify({
            title: "Success",
            message: "CV has been downloaded",
            type: "success",
          });
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
    downloadCoverLetter(data) {
      this.loading = true;
      this.$endpoint.downloadCoverLetter(data).then(
        () => {
          this.loading = false;
          this.$notify({
            title: "Success",
            message: "CV has been downloaded",
            type: "success",
          });
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
    handleClose() {
      this.application = {};
      this.dialog = false;
      this.dialog1 = false;
      this.dialog2 = false;
    },
  },
};
</script>
